#ivorfallIntro {
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}

.ivorfallLink {
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
    color: yellow;
}

.ivorfallLink:hover {
    padding-bottom: 0.25rem;
    border-bottom: 5px solid yellow;
}

.ivorfallDesc {
    font-weight: 400;
    font-size: 1rem;
    max-width: 50rem;
    padding: 0 2rem;
    padding-top: 1rem
}

.skillsLearnedTitle {
    font-size: 2rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 0.5rem;
    color: yellow;
}

.skillsLearnedList {
    font-weight: 400;
    font-size: 1rem;
    text-align: justify;
}

.youtubeVid {
    padding-top: 1rem;
}